import {VDOMN} from "./const";
class KeyGen {
	constructor () {
		const storage = {};
		return (key, type) => {
			type = type || "-notype";
			if (key) {
				return `${type}#${key}`;
			}
			storage[type] = storage[type] || 0;
			storage[type]++;
			return `${type}:idx(${storage[type]++})`;
		};
	}
}

function isAbsKey (val) {
	return (val || "").toString().startsWith("#");
}

function getContent (content) {
	return content !== null && !Array.isArray(content) ? content : null;
}

function isPropObj (val) {
	return val === Object(val) && !Array.isArray(val);
}

function serialize (val) {
	return JSON.stringify(val);
}

function diffObj (n, o, deep, path = [], result = []) {
	const np = isPropObj(n);
	const op = isPropObj(o);
	const upd = {};
	if ((!np && !op) || (deep && deep <= path.length)) {
		if (serialize(o) !== serialize(n)) {
			result.push({path, o, n});
		}
		return result;
	}

	if (np) {
		const nk = Object.keys(n);
		let i = 0;
		while (i < nk.length) {
			const key = nk[i];
			upd[key] = true;
			diffObj(n[key], o ? o[key] : undefined, deep, [...path, key], result);
			i++;
		}
	}
	if (op) {
		let i = 0;
		const ok = Object.keys(o);
		while (i < ok.length) {
			const key = ok[i];
			if (!upd[key]) {
				diffObj(undefined, o[key], deep, [...path, key], result);
			}
			i++;
		}
	}
	return result;
}


function find (group, i) {
	let s = 0;
	let e = group.length - 1;
	while (e - s > 1) {
		const h = Math.floor(s + ((e - s) / 2));
		if (group[h] > i) {
			e = h;
		}
		else {
			s = h;
		}
	}
	return e;
}

function diffOne (listOld, listNew, deep = true) {
	const pairs = {};
	const pairsChilds = {};
	const pairsNew = [];
	const pairsRemove = [];
	let list;
	list = listNew;
	if (list && list.length) {
		let idx;
		const length = list.length;
		const keyGen = new KeyGen();
		idx = 0;
		let key;
		while (idx < length) {
			const vnode = list[idx];
			const type = vnode[VDOMN.type];
			const content = vnode[VDOMN.content];
			const params = vnode[VDOMN.params] || {};
			const prevKey = key;
			key = keyGen(params.key, type);
			const pair = {move: true, key};
			pairs[key] = pair;

			if (Array.isArray(content)) {
				pairsChilds[key] = content;
			}

			pair.n = {idx, params, prevKey, content: getContent(content)};
			pairsNew.push(pair);
			idx++;
		}
	}
	list = listOld;
	if (list && list.length) {
		let idx;
		const length = list.length;
		const keyGen = new KeyGen();
		idx = 0;
		const groups = [];
		let maxlength = 0;
		let maxlengthidx;
		while (idx < length) {
			const vnode = list[idx];
			const type = vnode[VDOMN.type];
			const content = vnode[VDOMN.content];
			const params = vnode[VDOMN.params] || {};
			const key = keyGen(params.key, type);
			let pair = pairs[key];
			if (!pair) {
				pair = {move: false, key};
				pairs[key] = pair;
				pairsRemove.push(pair);
			}
			else {
				let added = false;
				const l = groups.length;
				const i = pair.n.idx;
				for (let gi = 0; gi < l; gi++) {
					const group = groups[gi];
					let length = group.length;
					if (group[length - 1] < i) {
						group.push(i);
						added = true;
						length++;
					}
					else if (group[0] < i) {
						const newgroup = group.slice(0, find(group, i));
						newgroup.push(i);
						added = true;
						length = newgroup.length;
						groups.push(newgroup);
					}

					if (length > maxlength) {
						maxlength = length;
						maxlengthidx = gi;
					}
				}
				if (!added) {
					groups.push([i]);
					if (maxlength === 0) {
						maxlength = 1;
						maxlengthidx = 0;
					}
				}
			}
			pair.o = {idx, params, content: getContent(content)};
			const childsOld = Array.isArray(content) ? content : null;
			const childsNew = pairsChilds[key];
			if (deep && pair.n && (childsNew || childsOld)) {
				pair.childs = diffOne(childsNew, childsOld, deep);
			}
			idx++;
		}

		if (maxlengthidx != null) {
			groups[maxlengthidx].forEach(i => {
				pairsNew[i].pair.move = false;
			});
		}
	}

	return [
		...pairsRemove,
		...pairsNew,
	];
}

/**
 * Производит сравнение виртуальных DOM деревьев
 *
 * @param {VDOMNode} n новое виртуальное DOM дерево
 * @param {VDOMNode} o старое виртуальное DOM дерево (из которого планируется получить новое)
 * @return {DiffResult} результат сравнения, в виде дерева операций которые нужно произвести над старым деревом чтобы получить новое
 */

export default function diff (n, o, deep = true) {
	return diffOne(n, o, deep);
}
